body{
  display: block;
  background: #20232A;
  margin: 0;
  padding: 0;
}
strong{
  white-space: pre;
}
.root_menu{
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overflow: auto;
  position: absolute;
  right: 0;
}
.App {
  text-align: left;
  width: 300px;
  padding: 0;
  margin: 0 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  right: 0;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
  overflow-y: overlay;
  overflow-x: hidden;
}
.App h1{
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 48px;
  display: flex;
  align-items: left;
  color: #FFFFFF;
  margin: 0 0 27px 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


