.auth_block{
  /* transition: all 5s linear; */
  display: flex;
  margin: -20px 0;
  align-items: flex-end;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: flex-start;
}
.auth_block li{
  display: block;
  transition: transform .5s ease-in-out;
}
.auth_block li:nth-child(1).show{
  transform: translate3d(0px, 0px, 0px);
}
.auth_block li:nth-child(2).show{
  transform: translate3d(-300px, 0px, 0px);
}
.auth_block li:nth-child(3).show{
  transform: translate3d(-600px, 0px, 0px);
}
.auth_block li:nth-child(1).hide{
  transform: translate3d(300px, 0px, 0px);
}
.auth_block li:nth-child(2).hide{
  transform: translate3d(0px, 0px, 0px);
}
.auth_block li:nth-child(3).hide{
  transform: translate3d(-300px, 0px, 0px);
}
.auth_block li.show{
  transition-delay: .5s;
 /* animation: show 5s ease-in-out; */
}
.auth_block li.hide{
  transition-delay: 0.25s;
  pointer-events: none;
  /* display: none; */
}
.auth_block form div{
  transition-delay: 0.2s;
  transition: transform .5s ease-in-out;
}
.auth_block .show form div{
  transform: translate3d(0px, 0px, 0px);
}
.auth_block .hide form div{
  transform: translate3d(300px, 0px, 0px);
}
.auth_block .hide .btn_left button{
  transition: transform .3s ease-in-out;
  transform: translate3d(300px, 0px, 0px);
  transition-delay: 0.05s;
}
.auth_block .hide .btn_left button:nth-child(2){
  transition-delay: 0s;
}
.auth_block form div:nth-child(2){
  transition-delay: 0.15s;
}
.auth_block form div:nth-child(3){
  transition-delay: 0.1s;
}
.auth_block form div:nth-child(4){
  transition-delay: 0.05s;
}
.auth_block form div:nth-child(5){
  transition-delay: 0s;
}


nav{
  display: block;
  transition: all 1s linear;
  max-height: 100vh;
}
form{    
  max-width: 300px;
  width: 300px;
  margin: 0 auto;
}
.input-wrap {
  height: 44px;
  position: relative;
  margin-bottom: 40px;
  transition: all .25s linear;
}

.btn-wrap {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-pack: end;
  -webkit-box-pack: end;
  justify-content: flex-end;
  margin-top: 25px;
  margin-bottom: 0px;
}
.input-wrap input {
  -webkit-transition: all .25s linear;
  -o-transition: all .25s linear;
  transition: all .25s linear;
  background: none;
  padding: 0;
  line-height: 43px;
  border: 0;
  text-align: left;
  display: block;
  color: #fff;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid #CF1C5D;
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  
  color: #FFFFFF;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active,
input:-webkit-autofill:focus {
    background-color: #FFFFFF !important;
    color: rgb(0, 3, 185) !important;
    -webkit-box-shadow: 0 0 0 1000px #202429  inset !important;
    -webkit-text-fill-color: #cf1c5d !important;
}
.input-wrap input:-internal-autofill-selected {
  appearance: menulist-button;
  background-color: #202429 !important;
  background-image: none !important;
  color: #FFFFFF !important;
  color: -internal-light-dark(white, white) !important;
}
.input-wrap input:focus {
  box-shadow: 0 2px 0px 0px #cf1c5d;
}
.input-wrap input::placeholder {
	color: rgba(255, 255, 255, 0);
  }


  .input-wrap .placeholder {
    -webkit-transition: all .25s linear;
    -o-transition: all .25s linear;
    transition: all .25s linear;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    text-align: left;
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 22px;
    line-height: 26px;
    text-transform: inherit;
    color: #FFFFFF;
}
.input-wrap input:focus+label {
  font-weight: 600;
}

.input-wrap .placeholder {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: left top;
    -ms-transform-origin: left top;
    transform-origin: left top;
}
.input-wrap input:focus+.placeholder, .input-wrap input:not(:placeholder-shown)+.placeholder, .model-window .input-wrap input:focus+.placeholder, .model-window .input-wrap input:not(:placeholder-shown)+.placeholder {
  -webkit-transform: scale(0.8);
  -ms-transform: scale(0.8);
  transform: scale(0.8);
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
  /* color: #2b3942; */
  top: -15px;

  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  text-transform: inherit;

  color: #FFFFFF;

  opacity: 0.5;
}




