.btn-wrap .btn svg{
  transition: all 0.25s linear;
  transform: translateX(0px);
  width: 45px;
  height: 45px;
  margin: 5px;
  display:inline;
  fill:none;
  stroke:#DA1C5C;
  stroke-width:0.7;
  stroke-linecap:round;
  stroke-linejoin:round;
  stroke-miterlimit:4;
  stroke-dasharray:none;
  stroke-opacity:1;
  image-rendering:optimizeSpeed
}

.btn-wrap .btn.prew {
  transform: scale(-1, 1);
}
.btn-wrap .btn.next{
  padding-right: 5px;
}
.btn.prew:active,
.btn.next:active{
  box-shadow: none;
  background: rgba(0, 0, 0, 0.2);
}
.btn-wrap .btn:hover svg{
  transform: translateX(4px);
  stroke-width:0.8;
}
.btn-wrap .btn:hover path{
  stroke-dasharray: 25;
  stroke-dashoffset: 0;
  animation: dash 1.5s ;
}
.btn-wrap .btn.submitted path{
  stroke-dasharray: 25;
  stroke-dashoffset: 0;
  animation: dash 1s infinite;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dashoffset: 25;
  }
  100% {
    stroke-dashoffset: 50;
  }
}

*:focus {
  outline: none;
}
button, input, optgroup, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
  -webkit-appearance: none;
}
*::selection {
  background: #c4e7ff;
}

