#path24{ 
  stroke-dasharray: 25;
  stroke-dashoffset: 25;
  animation: dash2 1.5s cubic-bezier(0.65, 0.05, 0.36, 1) infinite;
  display: inline;
  fill: none;
  stroke: #ff8600;
  stroke-width: 0.6;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 4;
  stroke-opacity: 0.59919;
}

@keyframes dash2 {
  0% {
    stroke-dashoffset: -25;
  }
  50% {
    stroke-dashoffset: 0;
  }
  95% {
    stroke-dashoffset: 25;
  }
  100% {
    stroke-dashoffset: 25;
  }
}
