.paginator{
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 10px;
  width: 100%;
  height: 28px;
  z-index: 200;
}
.paginator div{
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  padding: 0px;
  width: inherit;
  font-size: 0;
  transition: all 0.5s ease-in-out;
  height: 100%;
}

.paginator div::after{
  content: "";
  position: absolute;
  transition: opacity 0.2s ease-in-out;
  display: block;
  width: 8px;
  height: 8px;
  background: #db1c5c94;
  border-radius: 50%;
  opacity: 0.5;
  transform-origin: center center;
}
.paginator div:hover::after{
  opacity: 1;
}
.paginator div.active::after{
  opacity: 0;
}
.paginator div.active{
  background: #db1c5c;
  font-size: 21px;
}

.paginator i{
  position: absolute;
  left: 0;
  transition: width 0.3s cubic-bezier(0, -0.1, 0.5, 1.5);
  display: block;
  height: 100%;
  width: 42px;
  background: #db1c5d61;
}

