, rgba(0, 0, 0, 0) 40%, #2b3135 50%); */
  display: table;
  box-shadow: 0px 1px 3px -3px inset white, 0px 3px 2px -2px inset #2b3135;
  border-radius: 12px 12px 0 0;
}
.resizer.top::before {
  content: '';
  width: 80px;
  height: 5px;
  background: #da1c5b;
  box-shadow: 0 -1px 1px -1px white inset, 0 1px 2px -1px black inset;
  border-radius: 100px;
  margin: 20px auto;
  cursor: ns-resize;
  display: table;
  backdrop-filter: blur(1.5px);
  opacity: 0.5;
  transition: opacity 0.3s ease-in-out;
}
@media (hover: hover) {
  .resizer.top:hover::before {
    opacity: 1;
  }
}
.resizer.top.active::before {
  opacity: 1;
}



.resizer.top.active::after{
  content: '';
  width: 100%;
  height: 45px;
  background-color: #da1c5b;
  border-radius: 10px;
  animation: pulse 0.5s ease-out;
  transform: scale(0);
  position: absolute;
  margin: 0px auto;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  opacity: 0.5;
 }
 @keyframes pulse {
  to {
   transform: scale(2);
   opacity: 0.1;
  }
 }
 
